<template>
  <div class="icon-link-item">
    <router-link v-if="itemType === 'link'">
      <div class="icon-link-icon">
        <img :src="iconUrl" alt="">
      </div>
      <div class="icon-link-title">
        {{title}}
      </div>
    </router-link>
    <template v-else>
      <div class="icon-link-icon">
        <img :src="iconUrl" alt="">
      </div>
      <div class="icon-link-title">
        {{title}}
      </div>
    </template>
  </div>
</template>
<script>
  export default {
    name: 'icon-link-item',
    props: ['routerParams', 'title', 'iconUrl', 'itemType'],
    data() {
      return {}
    }
  }
</script>

<style>

</style>



