<template>
  <div class="home-container">
    <div class="home-banner">
      <img src="../../assets/banner2.jpg" alt="" style="width: 100%">
    </div>
    <div class="home-notice">
      <Title2
        :title="'公告：mail发布了新需求了'"
      />
    </div>
    <div class="link-container clearfix">
      <icon-link-item
        class="fl"
        :class="{mr20:index%4!==3}"
        v-for="( item, index ) in links"
        :key="index"
        :itemType="item.type"
        :title="item.title"
        :iconUrl="item.iconUrl"
      />
    </div>
    <div class="daily-ac clearfix">
      <div class="daily-sigin fl">
        <div class="daily-title1">每日签到</div>
        <div class="daily-title1-sub">持续签到有红包</div>
      </div>
      <div class="daily-invite fl">
        <div class="daily-title1">邀请有奖</div>
        <div class="daily-title1-sub">立刻招徒赚小钱</div>
      </div>
    </div>
    <div class="more-task-container">
      <Title1
        title="更多任务"
        subTitle="More tasks"
      />
      <div class="task-bg"></div>
    </div>
  </div>
</template>
<script>
  import IconLinkItem from '../../components/item/icon-link-item.vue'
  import { Title1, Title2 } from '../layout/components'
  import * as icons from '../../iconLink/home.js'
  export default {
    name: 'name',
    data() {
      return {
        links: [
          {
            type: 'wait',
            title: '最新任务',
            iconUrl: icons.NEWTASK
          },
          {
            type: 'wait',
            title: '高额任务',
            iconUrl: icons.MOREMONEYTASK
          },
          {
            type: 'wait',
            title: '拼团模式',
            iconUrl: icons.SPELL
          },
          {
            type: 'wait',
            title: '任务分类',
            iconUrl: icons.TASKHALL
          },
          {
            type: 'wait',
            title: '一元夺宝',
            iconUrl: icons.UNITARY
          },
          {
            type: 'wait',
            title: '新闻资讯',
            iconUrl: icons.NEWS
          },
          {
            type: 'wait',
            title: '渠道合作',
            iconUrl: icons.CHANNELCO
          },
          {
            type: 'wait',
            title: '关于我们',
            iconUrl: icons.ABOUTUS
          }
        ]
      }
    },
    components: {
      Title1,
      Title2,
      IconLinkItem
    }
  }
</script>

<style>

</style>



